import React from "react";
import Container from "react-bootstrap/Container";
import Card from "react-bootstrap/Card";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Carousel from "react-bootstrap/Carousel";

import "./Portfolio.css";

function Portfolio() {
  return (
    <Container>
      <Card id="portfolio-card">
        <Carousel>
          <Carousel.Item>
            <a href="https://github.com/highwolfx/Weather" target="_blank">
              <img
                className="d-block w-100"
                src={process.env.PUBLIC_URL + "images/code/weather.png"}
                alt="Weather Dashboard"
              />
            </a>
            <Carousel.Caption id="caption-box">
              <h3>Weather Dashboard</h3>
              <p>
                A dashboard where you can look at weather conditions from
                searched cities
              </p>
            </Carousel.Caption>
          </Carousel.Item>
          <Carousel.Item>
            <a
              href="https://github.com/highwolfx/Password-Generator"
              target="_blank"
            >
              <img
                className="d-block w-100"
                src={process.env.PUBLIC_URL + "images/code/password.png"}
                alt="Password Generator"
              />
            </a>

            <Carousel.Caption id="caption-box">
              <h3>Password Generator</h3>
              <p>
                Allows users to generate passwords with multiple options, such
                as characters and length
              </p>
            </Carousel.Caption>
          </Carousel.Item>
          <Carousel.Item>
            <a href="https://github.com/highwolfx/Quiz" target="_blank">
              <img
                className="d-block w-100"
                src={process.env.PUBLIC_URL + "images/code/quiz.png"}
                alt="Anime Quiz"
              />
            </a>

            <Carousel.Caption id="caption-box">
              <h3>Anime Quiz</h3>
              <p>
                A timed multiple choice quiz game on various topics anime
                related
              </p>
            </Carousel.Caption>
          </Carousel.Item>
          <Carousel.Item>
            <a
              href="https://github.com/highwolfx/burger-lounge"
              target="_blank"
            >
              <img
                className="d-block w-100"
                src={process.env.PUBLIC_URL + "images/code/burgers.png"}
                alt="Burger Tracker"
              />
            </a>
            <Carousel.Caption id="caption-box">
              <h3>Burger Lounge</h3>
              <p>
                A deployed app for keeping track of the burgers you've eaten and
                want to eat
              </p>
            </Carousel.Caption>
          </Carousel.Item>
          <Carousel.Item>
            <a
              href="https://github.com/highwolfx/CMS-employees"
              target="_blank"
            >
              <img
                className="d-block w-100"
                src={process.env.PUBLIC_URL + "images/code/cms.png"}
                alt="Employee Management System"
              />
            </a>
            <Carousel.Caption id="caption-box">
              <h3>Employee Management System</h3>
              <p>A node based employee management system</p>
            </Carousel.Caption>
          </Carousel.Item>
        </Carousel>
      </Card>
    </Container>
  );
}

export default Portfolio;
