import React from "react";
import Container from "react-bootstrap/Container";
import Card from "react-bootstrap/Card";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import "./About.css";

function About() {
  return (
    <Container>
      <Row>
        <Col lg={2}></Col>
        <Col lg={8}>
          <Card className="text-card" id="card-bg">
            <Row>
              <Col className="card-image" lg={4}>
                <img
                  src={process.env.PUBLIC_URL + "images/profile-picture.jpg"}
                  alt="Sean Chang Profile Picture"
                />
              </Col>
              <Col className="english-text" lg={7}>
                Hello! My name is Sean, and welcome to my portfolio page! I
                graduated with a B.S. in Biochemistry/Chemistry from University
                of California, San Diego in 2019. In my free time, I like to
                take photographs and edit them. In addition, I like keeping up
                with tech news, such as computer component releases, smartphone
                news, and etc. I am a native English speaker and am fluent in
                speaking Mandarin Chinese. I am conversational in Japanese, and
                can understand conversational Cantonese, but am not able to
                speak it.
              </Col>
            </Row>
          </Card>
          <Card className="text-card" id="card-bg">
            <Row>
              <Col className="card-image" lg={4}>
                <img
                  src={process.env.PUBLIC_URL + "images/FB-profile-picture.jpg"}
                  alt="Sean Chang picture backlit at beach"
                />
              </Col>
              <Col className="english-text" lg={7}>
                你好！我的名字是張羽生，歡迎到我的作品集網頁！我是2019年從加州公立大學聖地亞哥分校生物化學專業畢業的。有空的時候，我喜歡攝影和修改相片。此外，我喜歡跟著科技消息，比如電腦零件，智能手機，等等。我的母語是英文。中文講寫沒大問題。我也可以講基本日文。廣東話，我可以聽但不能講。
              </Col>
            </Row>
          </Card>
        </Col>
        <Col lg={2}></Col>
      </Row>
    </Container>
  );
}

export default About;
