import React from "react";
import Container from "react-bootstrap/Container";
import Navbar from "react-bootstrap/Navbar";
import Nav from "react-bootstrap/Nav";
import {
  AiFillLinkedin,
  AiOutlineGithub,
  AiFillFacebook,
  AiFillInstagram,
} from "react-icons/ai";
import "./BottomBar.css";

function BottomBar() {
  return (
    <Container>
      <Navbar bg="dark" variant="dark" expand="lg" fixed="bottom">
        <Nav className="ml-auto justify-content-end">
          <Nav.Link
            href="https://www.linkedin.com/in/seanchangx/"
            target="_blank"
          >
            <AiFillLinkedin size="2rem" />
          </Nav.Link>
          <Nav.Link href="https://github.com/highwolfx" target="_blank">
            <AiOutlineGithub size="2rem" />
          </Nav.Link>
          <Nav.Link href="https://www.facebook.com/SeanYChang" target="_blank">
            <AiFillFacebook size="2rem" />
          </Nav.Link>
          <Nav.Link href="https://www.instagram.com/highwolfx/" target="_blank">
            <AiFillInstagram size="2rem" />
          </Nav.Link>
        </Nav>
      </Navbar>
    </Container>
  );
}

export default BottomBar;
